<template>
  <div class="home">
    <div v-for="url in imgUrls" :key="url">
      <img class="bg-img" :src="url" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'home',
  data () {
    return {
      imgUrls: []
    }
  },
  created () {
    for (let i = 1; i < 24; i++) {
      this.imgUrls.push(require(`../assets/image/20193102249128551_${i}.jpg`))
    }
  }
}
</script>
<style scoped>
.bg-img {
  max-width: 1133px;
  width: 100%;
}
.home {
  background: url(../assets/image/bg.jpg);
}
</style>
